import React from 'react'
import { Link } from 'react-router-dom';
import MaterialIcon, { colorPalette } from 'material-icons-react';

class Content extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            activeMenu: 'home',
            elementToRender: null
        }
    }
    componentDidMount() {
        this.setState({ elementToRender: this.aboutMe() })
    }

    aboutMe() {
        return (<div className="row text-left" style={{ margin: '20px 60px 0 60px', padding: '20px', display: 'block' }}>
            <h2>
                my services
     </h2>
            <div className="row justify-content-center pt-5">
                <div className="col-6">
                    <MaterialIcon icon="computer" color="white " size="30px"></MaterialIcon>
                    <h6>
                        Development
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>
                        Building a custom tailored solution based on the technical requirements using the latest technologies like Angular, React etc.
                </p>
                </div>
                <div className="col-6">
                    <MaterialIcon icon="dns" color="white" size="30px"></MaterialIcon>
                    <h6>
                        Server architecture
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>
                        Designing a secure backend architecture according to the requirements that is cloud-native, cost-efficient and scalable using leading technologies like Java, NodeJS etc
                </p>
                </div>
            </div>
            <div className="row justify-content-center pt-2">
                <div className="col-6">
                    <MaterialIcon icon="reorder" color="white " size="30px"></MaterialIcon>
                    <h6>
                        Database Design
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>Designing DBs for easier and fast data access using SQL, MongoDb etc.</p>
                </div>
                <div className="col-6">
                    <MaterialIcon icon="flight_takeoff" color="white" size="30px"></MaterialIcon>
                    <h6>
                        Deployment
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>Deploying, configuring and  managing the application in the cloud. </p>
                </div>
            </div>

        </div>)
    }


    myWork() {
        return (<div className="row text-left" style={{ margin: '20px 60px 0 60px', padding: '20px', display: 'block' }}>
            <h2>
                my work
     </h2>
            <div className="row justify-content-center pt-5">
                <div className="col-6">
                    <MaterialIcon icon="grade" color="white " size="30px"></MaterialIcon>
                    <h6>
                        See a star
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>
                        A video chat app designed for an American company in which people can video chat with their favorite celebs.
                        Frontend in Ionic and Backend is fully cloud native using AWS Lambda.
                </p>
                </div>
                <div className="col-6">
                    <MaterialIcon icon="loop" color="white" size="30px"></MaterialIcon>
                    <h6>
                        Hireloop
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>
                        An app designed for a startup which helps companies hire people by engaging with them efficiently.
                        Frontend is built using Angular and backend is combination of AWS and Mongo DB cloud services.
                </p>
                </div>
            </div>
            <div className="row justify-content-center pt-2">
                <div className="col-6">
                    <MaterialIcon icon="face" color="white " size="30px"></MaterialIcon>
                    <h6>
                        KrishiVijay
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>A mobile app for the farmers, helping them get certifications for land and crops they yield.
                    Made using Ionic.</p>
                </div>
                <div className="col-6">
                    <MaterialIcon icon="devices" color="white" size="30px"></MaterialIcon>
                    <h6>
                        LogWatch
                </h6>
                    <p style={{ fontSize: '14px', color: colorPalette.grey.default }}>My personal project for monitoring different projects. Visualizing logs of the apps and searching them efficiently using ElasticSearch</p>
                </div>
            </div>

        </div>)
    }

    elementToRender = null;
    async componentWillReceiveProps(nextProps) {
        await this.setState({ activeMenu: nextProps.showData });
        if (this.state.activeMenu == 'home') {
            this.setState({ elementToRender: this.aboutMe() })
        }
        else if (this.state.activeMenu === 'work') {
            this.setState({ elementToRender: this.myWork() })
        }
        else {
            this.setState({ elementToRender: <div>HELLO</div> })
        }
    }

    githubProfile = 'https://github.com/rohancs95';
    linkedinprofile = 'https://www.linkedin.com/in/rohan-bhardwaj-640630116/'

    render() {
        return (
            <div className="inner-content" style={{ marginLeft: '50px', display: 'flex', backgroundColor: '#1D1F20', fontSize: '15px' }}>
                <img style={{ width: '300px', height: '80vh', objectFit: "cover", filter: 'grayscale(100%)' }} src={process.env.PUBLIC_URL + '/assets/profile1.png'}></img>
                <div style={{ width: '100%', color: 'white' }} className="row">
                    <div className="col-12">
                        <div className="row text-left" style={{ margin: '0 60px 0 60px', padding: '20px', display: 'block' }}>
                            <div style={{ display: 'flex' }}>
                                <h1 style={{ paddingRight: '10px' }}>
                                    Rohan Bhardwaj
                            </h1>
                                <a href={this.githubProfile} target='_blank' style={{ padding: '8px 0 0 20px', fontSize: '28px', cursor: 'pointer' }}><i className="fa fa-github"></i></a>
                                <a href={this.linkedinprofile} target='_blank' style={{ padding: '8px 0 0 20px', fontSize: '28px', cursor: 'pointer' }}><i className="fa fa-linkedin"></i></a>
                            </div>
                            <h6 className="mt-2 pb-3" style={{ color: '#007bff' }}>
                                25 years / Bengaluru / Software Developer
                     </h6>
                            <p className="mt-2" style={{ color: colorPalette.grey.default }}>
                                Passionate Full Stack Developer with 2+ years of experience in designing and implementing web and mobile applications. Self starter
                                on a vigorous path of constant growth, while consistently striving to work with my fellow peers to create easily digestible code and make a meaningful impact.
                            </p>
                        </div>
                        <hr />
                        {this.state.elementToRender
                        }
                    </div>
                </div>
            </div>
        )
    }
}
export default Content