import React from 'react'
import { Link } from 'react-router-dom';
import Sidebar from '../Sidebar/Sidebar';
import Content from '../Content/Content';
class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dataFromSideBar: null
        }
    }

    getDataFromSideBar = async (data) => {
        await this.setState({ dataFromSideBar: data }, () => {
            console.log(this.state)
        });
    }


    render() {
        return (
            <div>
                <div style={{ position: 'relative', border: '2px' }}>
                    <div className="sidebar">
                        <Sidebar callbackFromMain={this.getDataFromSideBar} />
                    </div>
                    <div className="content" >
                        <Content showData={this.state.dataFromSideBar} />
                    </div>
                </div>

            </div >
        )
    }
}
export default Main