import React from 'react'
import { Link } from 'react-router-dom';
import MaterialIcon, { colorPalette } from 'material-icons-react';

class Sidebar extends React.Component {

    constructor(props) {
        super(props);
        this.menuChange = this.menuChange.bind(this);
    }


    menuChange(data) {
        this.props.callbackFromMain(data);
    }

    render() {
        return (
            <div style={{ width: '100%', height: '30%', marginTop: '20vh' }} className="menu">
                <div className="mt-4" onClick={() => this.menuChange('home')}>
                    <MaterialIcon icon='home' color='white' size="24px"></MaterialIcon>
                </div>
                <div className="mt-4" onClick={() => this.menuChange('work')}>
                    <MaterialIcon icon='work' color='white' size="24px"></MaterialIcon>
                </div>
                <div className="mt-4" onClick={() => this.menuChange('contact')}>
                    <MaterialIcon icon='phone' color='white' size="24px"></MaterialIcon>
                </div>
                <div className="mt-4  " onClick={() => this.menuChange('education')}>
                    <MaterialIcon icon='school' color='white' size="24px"></MaterialIcon>
                </div>
            </div>
        )
    }
}
export default Sidebar